window.addEventListener("load", async () => {
    const admissionStatData = await getAdmissionStats();
    console.log(admissionStatData);
});


/**
 * @returns En array av antagningsstatistik.
 */
async function getAdmissionStats() {
    try {
        const resp = await fetch("../scripts/miun.json");
        return await resp.json();
    } catch (error) {
        console.error(error);
    }
}